.wrapper {
  background-color: white;
  padding: 10px;
  border-radius: 4px;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);
  max-width: 700px;
  width: 100%;
  margin: 0 auto;

  & > h2 {
    font-size: 32px;
    font-weight: bold;
    text-align: center;
    margin-bottom: .5em;
  }

  & > p {
    line-height: 1.4;
  }
}



